//////////////////////
// document
//////////////////////

html {
  // base font size for rem
  // 100% is 16px
  // value(%) = desired(px) * 100 / 16
  font-size: 50%; // 8px
  @media @min-md {
    font-size: 56.25%; // 9px
  }
  @media @min-xl {
    font-size: 62.5%; // 10px
  }
}
body {
  overflow-x: hidden;
  overflow-y: scroll;
  background-color: @white;
  .font-sans-regular();
  .font-medium();
  line-height: 1.6;
  color: @text;
}

//////////////////////
// size
//////////////////////

.font-tiny {
  font-size: 1.2rem;
}
.font-small {
  font-size: 1.3rem;
}
.font-medium {
  font-size: 1.4rem;
}
.font-big {
  font-size: 1.5rem;
}
.font-giant {
  font-size: 1.8rem;
}

//////////////////////
// headers
//////////////////////

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  .margin(y, layout);
  .margin(top, big);
  .margin(bottom, medium);
  .font-sans-semibold();
  line-height: 1.2;
  letter-spacing: 0;
  text-transform: none;
  & + & {
    .margin(top, medium);
  }
}

h1, .h1 {
  .margin(bottom, 2rem);
  font-size: 3.5rem;
  @media @min-sm {
    font-size: 4rem;
  }
}
h2, .h2 {
  .margin(bottom, big);
  font-size: 3.5rem;
}
h3, .h3 {
  font-size: 2.5rem;
}
h4, .h4 {
  font-size: 2rem;
}
h5, .h5 {
  font-size: 1.7rem;
}
h6, .h6 {
  font-size: 1.3rem;
  letter-spacing: .05em;
  text-transform: uppercase;
}

//////////////////////
// text
//////////////////////

p {
  .margin(y, layout);
  .margin(bottom, small);
}
strong {
  .font-sans-semibold();
}
small {
  font-size: .8em;
}
.muted {
  color: @grey-500;
}

// code

code {
  margin: 0 .05em;
  padding: .05em .05em;
  background-color: @grey-250;
  .font-monospace();
  font-size: .9em;
  color: @primary;
}
pre {
  .margin(y, layout);
  .margin(bottom, small);
  width: 100%;
  max-height: 37rem;
  overflow-x: hidden;
  overflow-y: auto;
  code {
    .padding(all, medium) !important;
    width: 100%;
    font-size: 12px;
    color: @text;
    line-height: 1.6;
  }
}

// reset

.reset-text {
  font-family: inherit !important;
  font-style: inherit !important;
  font-weight: inherit !important;
  letter-spacing: inherit !important;
  text-transform: inherit !important;
  font-size: inherit !important;
  color: inherit !important;
  line-height: inherit !important;
}

//////////////////////
// ul ol
//////////////////////

.default-style { // .default-style for default ul ol
  ul,
  ol {
    .margin(y, layout);
    .margin(bottom, small);
    padding-left: 2rem;
    > ul,
    > ol {
      .margin(y, medium);
    }
  }
  ul {
    list-style: disc;
    ul {
      list-style: circle;
    }
  }
  ol {
    list-style-type: decimal;
    ol {
      list-style-type: lower-alpha;
    }
  }
}

//////////////////////
// dl
//////////////////////

dl {
  .margin(bottom, small);
  .margin(bottom, layout);
  .font-medium();
  .dl-block();
  dt {
    .font-sans-semibold();
  }
}

// .dl-block

.dl-block {
  .margin-remove(top, tiny);
  dt {
    .margin(top, small);
  }
  dd {
    .margin(top, tiny);
  }
}

// .dl-line

.dl-line {
  .margin-remove(top, tiny);
  dt {
    .margin(top, tiny);
  }
  dd {
    .margin(left, small);
    .margin(top, tiny);
  }
}