//////////////////////
// contain
//////////////////////

.contain,
.contain-fluid {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.contain-fluid {
  max-width: @contain-fluid;
}

// generate-contain-responsive

.generate-contain-responsive();
.generate-contain-responsive(@i:length(@contains)) when (@i > 0) {
  .generate-contain-responsive(@i - 1);
  @contain: extract(@contains, @i);
  @breakpoint: extract(@contain, 1);
  @val: extract(@contain, 2);
  @overlay: extract(@overlay-space, @i);
  @breakpoint: extract(@overlay, 1);
  @val-overlay: extract(@overlay, 2);
  & when (@breakpoint = all) {
    .contain {
      width: @val;
    }
    .overlay .overlay-content {
      max-width: @val - @val-overlay * 2;
    }
  }
  & when not (@breakpoint = all) {
    @query-min: 'min-@{breakpoint}';
    @media @@query-min {
      .contain {
        width: @val;
      }
      .overlay .overlay-content {
        max-width: @val - @val-overlay * 2;
      }
    }
  }
}

// generate-contain-space

.generate-contain-space();
.generate-contain-space(@i:length(@contain-space)) when (@i > 0) {
  .generate-contain-space(@i - 1);
  @contain: extract(@contain-space, @i);
  @breakpoint: extract(@contain, 1);
  @val: extract(@contain, 2);
  & when (@breakpoint = all) {
    .contain,
    .contain-fluid {
      padding-right: @val;
      padding-left: @val;
    }
  }
  & when not (@breakpoint = all) {
    @query-min: 'min-@{breakpoint}';
    @media @@query-min {
      .contain,
      .contain-fluid {
        padding-right: @val;
        padding-left: @val;
      }
    }
  }
}

//////////////////////
// grid
//////////////////////

.all-auto {
  -webkit-flex-basis: auto;
  -ms-flex-preferred-size: 100%; // fix ie10 overflow
  flex-basis: auto;
  max-width: none;
  width: auto;
}

// grid
// USAGE: .all(6); .min(lg, 6); .max(xs, 6);

.all(@i) {
  @val: (percentage(@i / @columns));
  -webkit-flex-basis: @val;
  -ms-flex-preferred-size: @val;
  flex-basis: @val;
  max-width: @val;
  width: @val;
}
.min(@name, @i) {
  @query-min: 'min-@{name}';
  @val: (percentage(@i / @columns));
  @media @@query-min {
    -webkit-flex-basis: @val;
    -ms-flex-preferred-size: @val;
    flex-basis: @val;
    max-width: @val;
    width: @val;
  }
}
.max(@name, @i) {
  @query-max: 'max-@{name}';
  @val: (percentage(@i / @columns));
  @media @@query-max {
    -webkit-flex-basis: @val;
    -ms-flex-preferred-size: @val;
    flex-basis: @val;
    max-width: @val;
    width: @val;
  }
}

& when (@generate-layout) {

  // generate-grid

  .generate-grid(all; @columns);
  .generate-grid(@breakpoint; @columns; @i:1) when (@i =< @columns) {
    .generate-grid(@breakpoint; @columns; (@i + 1));
    .@{breakpoint}-@{i} {
      .all(@i);
    }
  }

  // generate-grid-responsive

  & when not (@generate-breakpoint = false) {
    .generate-grid-responsive();
    .generate-grid-responsive(@i:length(@generate-breakpoint)) when (@i > 0) {
      .generate-grid-responsive(@i - 1);
      @breakpoint: extract(@generate-breakpoint, @i);
      @query-min: 'min-@{breakpoint}';
      @media @@query-min {
        .generate-grid(@breakpoint; @columns);
        .@{breakpoint}-auto {
          -webkit-flex-basis: auto;
          -ms-flex-preferred-size: 100%; // fix ie10 overflow
          flex-basis: auto;
          max-width: none;
          width: auto;
        }
      }
    }
  }

}

//////////////////////
// flex
//////////////////////

.flex {
  .display(flex);
}

// flex-auto
// USAGE: .flex-auto(); .flex-auto(sm);

.flex-auto(@breakpoint:none) when (@breakpoint = none) {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
}
.flex-auto(@breakpoint:none) when (default()) {
  @query-min: 'min-@{breakpoint}';
  @media @@query-min {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
  }
}

& when (@generate-layout) {

  // generate-flex-auto

  .generate-flex-auto();
  .generate-flex-auto(@breakpoint:none) {
    & when (@breakpoint = none) {
      .flex-auto {
        .flex-auto() !important;
      }
    }
    & when not (@breakpoint = none) {
      @query-min: 'min-@{breakpoint}';
      @media @@query-min {
        .flex-auto-@{breakpoint} {
          .flex-auto() !important;
        }
      }
    }
  }

  // generate-flex-auto-responsive

  & when not (@generate-breakpoint = false) {
    .generate-flex-auto-responsive();
    .generate-flex-auto-responsive(@i:length(@generate-breakpoint)) when (@i > 0) {
      .generate-flex-auto-responsive(@i - 1);
      @breakpoint: extract(@generate-breakpoint, @i);
      .generate-flex-auto(@breakpoint);
    }
  }

}

// flex space
// USAGE: .flex-space(medium);
.flex-space(@name) {
  .by-key(@flex-space-horizontal, @name);
  @horizontal: @by-key;
  .by-key-alt(@flex-space-vertical, @name);
  @vertical: @by-key-alt;
  margin: -@vertical -@horizontal;
  > * {
    padding: @vertical @horizontal;
  }
}

& when (@generate-layout) {

  // generate-flex-space

  .generate-flex-space();
  .generate-flex-space(@i:length(@flex-space-horizontal)) when (@i > 0) {
    .generate-flex-space(@i - 1);
    @flex: extract(@flex-space-horizontal, @i);
    @name: extract(@flex, 1);
    .flex-space-@{name} {
      .flex-space(@name) !important;
    }
  }

}

//////////////////////
// direction
//////////////////////

// vertical
// USAGE: .vertical(); .vertical(sm);

.vertical(@breakpoint:none) when (@breakpoint = none) {
  .flex-column();
  .flex-wrap();
  &.reverse {
    .flex-column();
  }
  /*
  > .item {
    width: 100%;
  }
  > .fluid {
    -webkit-flex: 1 1 auto;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
  }
  */
}
.vertical(@breakpoint:none) when (default()) {
  @query-min: 'min-@{breakpoint}';
  @media @@query-min {
    .flex-column();
    .flex-wrap();
    &.reverse {
      .flex-column-reverse();
    }
    /*
    > .item {
      width: 100%;
    }
    > .fluid {
      -webkit-flex: 1 1 auto;
      -ms-flex: 1 1 auto;
      flex: 1 1 auto;
    }
    */
  }
}

// horizontal
// USAGE: .horizontal(); .horizontal(sm);

.horizontal(@breakpoint:none) when (@breakpoint = none) {
  .flex-row();
  .flex-nowrap();
  &.reverse {
    .flex-row-reverse();
  }
  /*
  > .item {
    width: 100%;
  }
  > .fluid {
    -webkit-flex: 1 1 auto;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
  }
  */
}
.horizontal(@breakpoint:none) when (default()) {
  @query-min: 'min-@{breakpoint}';
  @media @@query-min {
    .flex-row();
    .flex-nowrap();
    &.reverse {
      .flex-row-reverse();
    }
    /*
    > .item {
      width: auto;
    }
    > .fluid {
      -webkit-flex: 1 1 0%;
      -ms-flex: 1 1 0%;
      flex: 1 1 0%;
    }
    */
  }
}

& when (@generate-layout) {

  // generate-directions

  .generate-directions();
  .generate-directions(@breakpoint:none) {
    & when (@breakpoint = none) {
      .vertical {
        .vertical();
      }
      .horizontal {
        .horizontal();
      }
    }
    & when not (@breakpoint = none) {
      @query-min: 'min-@{breakpoint}';
      @media @@query-min {
        .vertical-@{breakpoint} {
          .vertical();
        }
        .horizontal-@{breakpoint} {
          .horizontal();
        }
      }
    }
  }

  // generate-directions-responsive

  & when not (@generate-breakpoint = false) {
    .generate-directions-responsive();
    .generate-directions-responsive(@i:length(@generate-breakpoint)) when (@i > 0) {
      .generate-directions-responsive(@i - 1);
      @breakpoint: extract(@generate-breakpoint, @i);
      .generate-directions(@breakpoint);
    }
  }

}

//////////////////////
// list
//////////////////////

.list-base() {
  .display(flex);
  //.flex-auto(); // @FIX flex inside flex take 100% but dont bugs negative margins
  // list
  .list-btn({
    .block();
    .text(left);
  });
  .list-btn-default({
    .btn();
    .text(left);
  });
}

// selectors

.list-btn(@rules) {
  > li  {
    > a, > button {
      @rules();
    }
  }
}
.list-btn-default(@rules) {
  > li  {
    > a, > button {
      &:not(.btn) {
        @rules();
      }
    }
  }
}

// .list-block

.list-block {
  .list-base();
  .flex-column();
  .flex-nowrap();
}

// .list-line

.list-line {
  .list-base();
  .flex-row();
  .flex-wrap();
}

// alignment

.list-left {
  .list-btn({
    .text(left) !important;
  });
}
.list-center {
  .list-btn({
    .text(center) !important;
  });
}
.list-right {
  .list-btn({
    .text(right) !important;
  });
}

// list space
// USAGE: .list-space(medium);

.list-space(@name) {
  .by-key(@list-space-horizontal, @name);
  @horizontal: @by-key;
  .by-key-alt(@list-space-vertical, @name);
  @vertical: @by-key-alt;
  margin: -@vertical -@horizontal;
  > * {
    margin: @vertical @horizontal;
  }
}

// generate-list-space

.generate-list-space();
.generate-list-space(@i:length(@list-space-horizontal)) when (@i > 0) {
  .generate-list-space(@i - 1);
  @list: extract(@list-space-horizontal, @i);
  @name: extract(@list, 1);
  .list-space-@{name} {
    .list-space(@name) !important;
  }
}

//////////////////////
// box
//////////////////////

.box {
  .display(flex);
  .flex-column();
  position: relative;
}